@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.logo{
  display: flex;
}
  
.logo input[type=text]{
  width: 350px;
  margin-left: 50px;
  margin-right: 10px;
  border-radius: 5px;
}
  
.top{
  background-color: rgb(255,153,0);
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo h5{
  font-family: 'Fredoka One', cursive;
}

#search_btn{
  position: relative;
  font-size: 22px;
  top: 5px;
  cursor: pointer;
}

.navbar_container{
    border: 1px solid rgb(214, 210, 210);
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.product_list{
    margin: 0 20px;
    color: black !important;
    text-transform: uppercase;
    font-family: 'Fredoka One', cursive;
}
.slideshow{
    width: 100%;
}

video{
    width: 100%;
}

.home_images{
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 400px 400px 400px;
    grid-gap: 10px;
    width: 95%;
    margin: auto;
}

.home_img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: 1s;
    border: 2px solid rgb(214, 211, 211);
}

.img_split{
    position: relative;
}

.overlay{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0;
    transition: 2s;
}

.content{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    color: 	white;
    text-align: center;
}

.content p{
    font-size: 2rem;
    font-weight: 800;
    color: black;
}

.img_split:hover .overlay{
    opacity: 1;
}

.img_split:hover .home_img{
    opacity: 0.4;
}

.info h1{
    text-transform: uppercase;
    font-weight: 800;
}

.youtube{
    position: relative;
    width: 99%;
    height: 500px;
    background-image: url('https://cloudfront-us-east-1.images.arcpublishing.com/octane/2T7JZSQ5XIROWCWVZDDZAYFFDQ.jpg');
    margin-left: 0.5%;
    margin-top: 10px;
    margin-bottom: 100px;
    background-color: red;
    background-blend-mode: multiply;
}

.youtube_button{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.info span{
    font-size: 17px;
    font-weight: 700;
}
.footers{
    color: white;
    padding: 20px;
    margin-top: 100px;
    background-color: black;
}

.social_media a{
    text-decoration: none;
    margin-right: 25px;
    color: white;
    font-size: 20px;
}

.contact a{
    text-decoration: none;
    margin-right: 15px;
    color: white;
}
.login_page{
    width: 100%;
    height: 88vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url('https://photos.motogp.com/2015/08/28/gbr_gp_0160_0.big.jpg');
    background-size: 100% 100%;
}

.login_details{
    width: 400px;
    padding: 20px;
    background-color: white;
    border: 2px solid rgb(228, 222, 222);
    border-radius: 10px;
    box-shadow: 6px 6px 10px black, -6px -6px 10px black;
}

.login_details h5{
    font-weight: 900 !important;
    text-transform: uppercase;
}

.forgot{
    position: relative;
    left:50%;
    font-size: 14px;
}

.login_details p{
    font-size: 14px; 
}

.signup_page{
    width: 100%;
    min-height: 88vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url('https://photos.motogp.com/2015/08/28/gbr_gp_0160_0.big.jpg');
    background-size: 100% 100%;    
}

.signup_details{
    width: 400px;
    padding: 20px;
    background-color: white;
    border: 2px solid rgb(228, 222, 222);
    border-radius: 10px;
    box-shadow: 6px 6px 10px black, -6px -6px 10px black;
}

.signup_details h5{
    font-weight: 900 !important;
    text-transform: uppercase;
}

.signup_details p{
    font-size: 14px; 
}
.main_heading{
    margin-top: 20px;
    text-transform: uppercase;
    font-weight: 900;
    background-color: #f3ec78;
    background-image: repeating-linear-gradient(45deg, rgb(50,65,75) 0%, rgb(50,65,75) 1%,rgb(193,81,0) 1%, rgb(193,81,0) 2%);
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}

.brand_para{
    font-family: 'Jost', sans-serif;
    word-spacing: 5px;
    line-height: 2;
    font-size: 20px;
    margin: 20px 20px;
    text-align: justify;
    font-weight: 400;
}

/* ============================================================================= */

.brand_images{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(190px,1fr));
    grid-gap:30px;
    width:60%;
    margin: 50px auto 50px;
    padding: 10px;
}

.brand_images_img{
    width: 100%;
    height: 100%;
    border: 6px solid rgb(60,118,138);
    border-radius: 10px;
}

.images h1{
    font-size: 3.5rem;
    margin-top: 60px;
    margin-bottom: 50px;
}

/* =============================================================================== */

.our_products{
    margin-top: 100px;
}

.brand_our_products{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px,1fr));
    grid-gap:30px;
    width:80%;
    margin: 50px auto 50px;
}

.brand_products{
    width: 100%;
    opacity: 0.8;
}

.brannd_split{
    position: relative;
}

.product_overlay{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0;
    background: linear-gradient(to top, #00d4ff, black);
    transition: 3s;
    border-radius: 20px;
}

.overlay_content{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    color: 	#2f4f4f;
    text-align: center;
    font-family: 'Ceviche One', cursive;
}

.overlay_content h3{
    font-size: 40px;
    color: white;
}

.brannd_split:hover .product_overlay{
    opacity: 0.8;
}


.our_products h1{
    font-size: 3.5rem;
    margin: 70px 0;
}

