.helmets{
    width: 100%;
    display: flex;
}

.helmet_sidebar{
    width: 17%;
    height: 630px;
}

/* ================================================================= */

.helmet_list{
    width: 83%;
}

.mt_brand{
    margin: 30px 20px 30px 20px;
}

.helmet_name{
    font-size: 15px;
    text-transform: uppercase;
}

.price{
    text-align: center;
    color: red;
}

.pruchasing_content{
    display: flex;
    align-items: center;
    position: relative;     
}

.row2{
    margin-top: 5px;
}

.card_list{
    margin: 5px 0;
}

.helmet_info{
    height: 250px;
    width: 80% !important;
    background-size: 100% 100%;
    transition: 1s;
    margin-left: 10% !important;
}

