.pants{
    width: 100%;
    display: flex;
}

.pants_sidebar{
    width: 17%;
    height: 630px;
}

.pants_filters h6{
    font-size: 17px;
    font-weight: 700;
}

/* ================================================================= */

.pants_list{
    width: 87%;
}

.pants_brand{
    margin: 30px 20px 30px 20px;
}

.pants_name{
    font-size: 15px;
    text-transform: uppercase;
}

.pants_price{
    text-align: center;
    color: red;
}

.pants_pruchasing_content{
    display: flex;
    align-items: center;
    position: relative;     
}

.row2{
    margin-top: 5px;
}

.pants_card_list{
    margin: 5px 0;
}

.pants_info{
    height: 250px;
    width: 80% !important;
    background-size: 100% 100%;
    transition: 1s;
    margin-left: 10% !important;
}

