.jackets{
    width: 100%;
    display: flex;
}

.jackets_filters{
    width: 15%;
    height: 630px;
    /* overflow-y: scroll; */
}

.jackets_filters h6{
    font-size: 17px;
    font-weight: 700;
}

.jackets_list{
    width: 85%;
    min-height: 500px;
}

/* ========================================================== */

.jacket_brand{
    margin: 30px 20px 30px 20px;
}

.jackets_name{
    font-size: 15px;
    text-transform: uppercase;
}

.jacket_price{
    text-align: center;
    color: red;
}

.jackets_pruchasing_content{
    display: flex;
    align-items: center;
    position: relative;     
}

.row2{
    margin-top: 5px;
}

.jacket_card_list{
    margin: 5px 0;
}

.jacket_info{
    height: 250px;
    width: 96% !important;
    background-size: 100% 100%;
    transition: 1s;
    margin-left: 2% !important;
}

