@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');

.logo{
  display: flex;
}
  
.logo input[type=text]{
  width: 350px;
  margin-left: 50px;
  margin-right: 10px;
  border-radius: 5px;
}
  
.top{
  background-color: rgb(255,153,0);
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo h5{
  font-family: 'Fredoka One', cursive;
}

#search_btn{
  position: relative;
  font-size: 22px;
  top: 5px;
  cursor: pointer;
}
