.forgotPassword_page{
    width: 100%;
    height: 88vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url('https://photos.motogp.com/2015/08/28/gbr_gp_0160_0.big.jpg');
    background-size: 100% 100%;
}

.forgotPassword_details{
    width: 400px;
    padding: 20px;
    background-color: white;
    border: 2px solid rgb(228, 222, 222);
    border-radius: 10px;
    box-shadow: 6px 6px 10px black, -6px -6px 10px black;
}

.forgotPassword_details h5{
    font-weight: 900 !important;
    text-transform: uppercase;
}

.otp_btn{
    display: flex;
    align-items: center;
    justify-content: center;
}

.forgotPassword_details p{
    font-size: 14px ; 
}
